/*translation*/
@keyframes animStar {
	from {
		transform: translateY(0px);
	}
	to {
		transform: translateY(-100rem);
	}
}

/*query small an medium mobiles*/
@media screen and (min-width: 320px) and (max-width: 424px) {
	/*change rem*/
	html {
		font-size: 12px;
	}
	/*badge-new-content*/
	.badge-new-content {
		display: grid;
		grid-template-rows: repeat(2, 1fr);
		align-items: center;
		grid-template-areas: 'form' 'badge';
	}

	/*translation*/
	@keyframes animStar {
		from {
			transform: translateY(0px);
		}
		to {
			transform: translateY(-200rem);
		}
	}
}

/*query large mobiles*/
@media screen and (min-width: 425px) and (max-width: 767px) {
	/*change rem*/
	html {
		font-size: 15px;
	}
	/*badge-new-content*/
	.badge-new-content {
		display: grid;
		grid-template-rows: repeat(2, 1fr);
		align-items: center;
		grid-template-areas: 'form' 'badge';
	}
}
